<template>
  <div>
    <search-box :searchData="searchData" :table="table"></search-box>
    <resize-table>
    <table class="table">
      <thead>
      <tr>
        <th width="7%">年级</th>
        <th width="7%">学科</th>
        <th width="7%">教师姓名</th>
        <th width="6%">听课次数</th>
        <th width="6%">教案个数</th>
        <th width="4%">时间</th>
        <th width="4%">序号</th>
        <th width="4%">课题</th>
        <th width="4%">目标</th>
        <th width="5%">重难点</th>
        <th width="6%">教学方法</th>
        <th width="6%">教学过程</th>
        <th width="6%">作业布置</th>
        <th width="6%">板书设计</th>
        <th width="6%">后记反思</th>
        <th width="4%">等级</th>
        <th width="12%">亮点与不足</th>
      </tr>
      </thead>
      <tbody>

      <tr v-for="item in table.list">
        <td>{{item.gradeName}}</td>
        <td>{{item.courseName}}</td>
        <td>{{item.name}}</td>
        <td>{{item.listenNum}}</td>
        <td>{{item.planNum}}</td>
        <td>
          <i v-if="item.lessonsTime === 'YES'" class="el-icon-check"></i>
          <i v-else class="el-icon-close"></i>
        </td>
        <td>
          <i v-if="item.lessonsNo === 'YES'" class="el-icon-check"></i>
          <i v-else class="el-icon-close"></i>
        </td>
        <td>
          <i v-if="item.lessonsTask === 'YES'" class="el-icon-check"></i>
          <i v-else class="el-icon-close"></i>
        </td>
        <td>
          <i v-if="item.lessonsTarget === 'YES'" class="el-icon-check"></i>
          <i v-else class="el-icon-close"></i>
        </td>
        <td>
          <i v-if="item.lessonsDifficulty === 'YES'" class="el-icon-check"></i>
          <i v-else class="el-icon-close"></i>
        </td>
        <td>
          <i v-if="item.lessonsMethod === 'YES'" class="el-icon-check"></i>
          <i v-else class="el-icon-close"></i>
        </td>
        <td>
          <i v-if="item.lessonsProcess === 'YES'" class="el-icon-check"></i>
          <i v-else class="el-icon-close"></i>
        </td>
        <td>
          <i v-if="item.lessonsHomework === 'YES'" class="el-icon-check"></i>
          <i v-else class="el-icon-close"></i>
        </td>
        <td>
          <i v-if="item.lessonsBoard === 'YES'" class="el-icon-check"></i>
          <i v-else class="el-icon-close"></i>
        </td>
        <td>
          <i v-if="item.lessonsPostscript === 'YES'" class="el-icon-check"></i>
          <i v-else class="el-icon-close"></i>
        </td>
        <td>
          <span :class="['assess', item.assessLevel]">{{$enums.AssessLevel.getName(item.assessLevel)}}</span>
        </td>
        <td>{{item.remark}}</td>
      </tr></tbody>
    </table>
    </resize-table>
    <no-data v-show="!table.noData"></no-data>
    <paging :data.sync="table"></paging>
  </div>

</template>
<script>
  export default {
    data() {
      return {
        searchData: [
          {type: 'select',title: '学期', width: '240px', notRemove: true, model: 'termId', option: []},
          {type: 'select',title: '年级', notRemove: true, model: 'gradeId', option: []},
          {type: 'select',title: '学科', notRemove: true, model: 'courseId', option: []},
        ],
        table:{
          url: '/prepareLessons/queryTeacherPrepareLessonsPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            termId: null,
            gradeId: null,
            courseId: null
          }
        },
      }
    },
    methods: {
      // 获取学期
      async getTermList(){
        let res = await this.$ajax({
          url: '/school/querySchoolTermList.do'
        })
        this.searchData[0].option = res.map(e=> {
          return {label: e.termName, value: e.schoolTermId}
        })
        res.forEach(e=>{
          if(e.isDefault === 'YES'){
            this.table.query.termId = e.schoolTermId
          }
        })
      },
      // 获取年级列表
      async getGradeList() {
        let res = await this.$ajax({
          url: '/grade/queryGradeList.do',
        })
        this.searchData[1].option = res.map(e=>{
          return {label: e.name, value: e.id}
        })
        this.table.query.gradeId = res[0].id
      },
      // 获取学科
      async subject(){
        let res = await this.$ajax({
          url: '/teachGroupCourse/queryCourseList.do',
        })
        this.searchData[2].option = res.map(e=>{
          return {value: e.id,label: e.name}
        })
        this.table.query.courseId = res[0].id
      },
    },
    async mounted(){
      await this.getTermList()
      await this.getGradeList()
      await this.subject()
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .table
    .el-icon-check
      vertical-align: middle
    .el-icon-close
    .assess
      display: inline-block
      width: 24px
      height: 16px
      line-height: 16px
      text-align: center
      color: #fff
      font-size: $font-small
      border-radius: 2px
    .LEVEL_A
      background: $success-c
    .LEVEL_B
      background: $main-c
    .LEVEL_C
      background: $warning

</style>
