<template>
  <div class="index">
    <div class="search-box">
      <div class="item">
        <span class="text">学期</span>
        <el-select @change="getDetail" class="term-input" v-model="schoolTermId">
          <el-option v-for="item in termList" :key="item.schoolTermId"
                     :label="item.termName" :value="item.schoolTermId"
          ></el-option>
        </el-select>
      </div>
    </div>
    <resize-table>
    <table class="table main-table mt20">
      <thead>
      <tr>
        <th width="9%">学科</th>
        <th width="9%">教研活动次数</th>
        <th width="10%" v-for="item in detail.gradeList">{{item.name}}备课次数</th>
        <th width="9%">备课次数总和</th>
        <th width="9%">备课平均次数</th>
        <th width="7%">等级</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="item in detail.statisticsList">
        <td>{{item.teachGroupName}}</td>
        <td>
          {{item.activityNum || 0}}
          <i v-show="item.activityNum" @click="seeList('ACTIVITY_TEACH_GROUP',item)" class="oper see el-icon-view"></i>
        </td>
        <td v-for="i in item.gradeStatisticsList">
          {{i.prepareLessonsNum || 0}}
          <!--<span  class="oper see">查看</span>-->
          <i v-show="i.prepareLessonsNum" @click="seeList('ACTIVITY_PREPARE_LESSONS',item,i)" class="oper see el-icon-view"></i>
        </td>
        <td>{{item.prepareLessonsNum || 0}}</td>
        <td>{{item.prepareLessonsAvg || 0}}</td>
        <td>
          <span :class="['assess', item.assessLevel]">{{$enums.AssessLevel.getName(item.assessLevel) || '--'}}</span>
        </td>
      </tr>
      </tbody>
    </table>
    </resize-table>
    <!--活动列表弹窗-->
    <el-dialog title="活动列表" class="dialog" :visible.sync="listDialog" width="600px">
      <resize-table>
      <table class="table table-list">
        <thead>
        <tr>
          <th width="50%">活动名称</th>
          <th width="25%">活动时间</th>
          <th width="25%">教研组/备课组</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in activeList">
          <td><span @click="showDetailDialog(item)" class="oper">{{item.activityName}}</span></td>
          <td>{{item.activityDate | substrTime}}</td>
          <td v-if="activeList.length > 0">{{activeList[0].teachGroupName}}</td>
        </tr>
        </tbody>
      </table>
      </resize-table>
      <div class="btns">
        <div @click="listDialog = false" class="btn-submit">返回</div>
      </div>
    </el-dialog>
    <!--活动详情弹窗-->
    <el-dialog title="活动详情" class="dialog" :visible.sync="detailDialog" width="600px">
      <resize-table>
      <table v-if="activeDetail" class="table detail-table">
        <thead>
          <tr>
            <td class="title">教研组</td>
            <td class="content">{{activeDetail.teachGroupName}}</td>
            <td class="title">活动时间</td>
            <td class="content">{{activeDetail.activityDate | substrTime}}</td>
          </tr>
         </thead>
        <tbody>
          <tr>
            <td class="title">活动名称</td>
            <td colspan="3" class="content">{{activeDetail.activityName}}</td>
          </tr>
          <tr>
            <td class="title">举办单位</td>
            <td colspan="3" class="content">{{activeDetail.activitySponsor}}</td>
          </tr>
          <tr>
            <td class="title">活动地点</td>
            <td colspan="3" class="content">{{activeDetail.activityLocation}}</td>
          </tr>
          <tr>
            <td class="title">活动内容</td>
            <td colspan="3" class="content">{{activeDetail.activityContent}}</td>
          </tr>
          <tr>
            <td class="title">参会人员</td>
            <td colspan="3" class="content">{{activeDetail.teacherList.map(e=> e.name).join()}}</td>
          </tr>
        </tbody>
      </table>
      </resize-table>
      <el-carousel v-if="activeDetail">
        <el-carousel-item class="carousel" :key="key" v-for="(item,key) in activeDetail.attachmentList">
          <img :src="item.fileUrl" alt="">
        </el-carousel-item>
      </el-carousel>
    </el-dialog>
  </div>

</template>
<script>
  export default {
    data() {
      return {
        // 学期列表
        termList: [],
        // 选择的学期
        schoolTermId: null,
        detail: {statisticsList: []},
        // 活动列表弹窗
        listDialog: false,
        // 活动列表数据
        activeList: [],
        // 活动详情弹窗
        detailDialog: false,
        // 活动详情数据
        activeDetail: null
      }
    },
    methods: {
      // 获取学期
      async getTermList() {
        this.termList = await this.$ajax({
          url: '/school/querySchoolTermList.do'
        })
        this.termList.forEach(e=>{
          if(e.isDefault === 'YES'){
            this.schoolTermId = e.schoolTermId
          }
        })
      },
      // 查看活动列表
      async seeList(type, item, i) {
        this.listDialog = true
        let res = await this.$ajax({
          url: '/teachGroupActivity/queryTeachGroupActivityPage.do',
          el: '.table-list',
          query: {
            activityType: type,
            termId: this.schoolTermId,
            teachGroupId: item.teachGroupId,
            gradeId: i ? i.gradeId : null,
            pageSize: 100000,
            pageNo: 1
          }
        })
        this.activeList = res.list
        this.activeList[0].teachGroupName = item.teachGroupName
      },
      async getDetail() {
        this.detail = await this.$ajax({
          url: '/teachGroupActivity/queryTeachGroupStatisticsList.do',
          el: '.table',
          query: {
            schoolTermId: this.schoolTermId
          }
        })
      },
      // 展现活动详情弹窗
      async showDetailDialog(item) {
        // 查看活动详情
        this.detailDialog = true
        this.activeDetail = await this.$ajax({
          url: '/teachGroupActivity/getTeachGroupActivity.do',
          el: '.table-detail',
          query: {
            id: item.id
          }
        })
      }
    },
    async mounted() {
      await this.getTermList()
      this.getDetail()
    }
  }
</script>
