<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-tabs v-model="tabs">
        <el-tab-pane label="个人备课" name="personal"></el-tab-pane>
        <el-tab-pane label="集体备课" name="group"></el-tab-pane>
      </el-tabs>
      <div class="btn-imp" v-show="tabs === 'personal'">
        <!--个人备课模板-->
        <a download class="f-small main-c mr5" href="../gy-files/template/个人备课导入模板.xlsx">下载模板</a>
        <label for="importDom">
          <div class="btn-main btn-upload mr15 ml5">批量导入</div>
        </label>
        <input accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
               @change="upload($event)" ref="upload" id="importDom" type="file" hidden>
      </div>

      <transition-group mode="out-in" name="el-fade-in-linear">
        <personal class="tabs-box"  key="1" v-if="tabs === 'personal'"></personal>
        <group class="tabs-box" key="2" v-if="tabs === 'group'"></group>
      </transition-group>
    </main-part>
  </div>
</template>
<script>
  import personal from './Personal'
  import group from './Group'
  export default {
    components:{
      group,
      personal
    },
    data() {
      return {
        nav: [{name: "备课检查", path: ""}],
        tabs: 'personal',
      }
    },
    methods: {
      // 导入
      async upload(e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        this.$refs.upload.value = null;
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/import/importTeacherPrepareLessons.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        if(res.errMsgList && res.errMsgList.length){
          this.$message.error(res.errMsgList[0])
        }else{
          this.$remind('导入成功')
        }
        this.$search(this.table)
      },
    },
  }
</script>
